<template>
  <div class="all">
    <a id="TheFinalSkill" class="noSelect"> by TheFinalSkill</a>
    <div id="landing-container" class="container-onepage-height">
      <div v-if="true">
        <b-button
          id="new-challenges"
          variant="success"
          @click="generateNewChallenges()"
        >
          Neue Challenges
        </b-button>
        <b-button
          id="new-game-challenge"
          variant="primary"
          v-b-modal.gameChallengeModal
          @click="generateNewGameChallenge()"
          v-b-tooltip.hover
          title="Klicke hier um eine neue Lobby Challenge anzuzeigen."
        >
          Neue Lobby Challenge
        </b-button>
        <b-button
          id="feedback-button"
          variant="success"
          v-b-modal.feedbackModal
          v-b-tooltip.hover
          title="Klicke hier um neue Vorschläge einzureichen."
        >
          Vorschlag einreichen
        </b-button>
      </div>
      <div id="modals">
        <b-modal
          ref="gameChallengeModal"
          id="gameChallengeModal"
          title="Lobbybezogene Challenge"
          hide-footer
        >
          {{ gameChallenge }}
          <b-button
            class="mt-3"
            variant="outline-danger"
            block
            @click="hideModal('gameChallengeModal')"
            >Schließen</b-button
          >
        </b-modal>
        <b-modal
          ref="mapModal"
          id="mapModal"
          size="xl"
          header-bg-variant="dark"
          body-bg-variant="dark"
          header-text-variant="light"
          hide-footer
        >
          <center><img width="1000" :src="mapModalImgSrc" /></center>
          <b-button
            class="mt-3"
            variant="outline-danger"
            block
            @click="hideModal('mapModal')"
            >Schließen</b-button
          >
        </b-modal>

        <b-modal
          title="Neuen Vorschlag einreichen"
          ref="feedbackModal"
          id="feedbackModal"
          size="lg"
          header-bg-variant="dark"
          body-bg-variant="dark"
          header-text-variant="light"
          hide-footer
        >
          <center>
            <iframe
              src="https://docs.google.com/forms/d/e/1FAIpQLScOgb9D7_sM-efSyeAZc8ew6SQvjDrMto7zsxME_rTpNA17xA/viewform?embedded=true"
              width="600"
              height="700"
              frameborder="0"
              marginheight="0"
              marginwidth="0"
              >Wird geladen…</iframe
            >
          </center>
          <b-button
            class="mt-3"
            variant="outline-danger"
            block
            @click="hideModal('feedbackModal')"
            >Schließen</b-button
          >
        </b-modal>

        <b-modal
          ref="challengeListModal"
          id="challengeListModal"
          size="lg"
          header-text-variant="light"
          body-text-variant="light"
          :body-bg-variant="challengeListModalColor"
          :header-bg-variant="challengeListModalColor"
          scrollable
          hide-footer>
          <p :key="challenge" v-for="challenge in challengeList">
            {{challenge}}
          </p>
          <b-button class="mt-3" variant="success" block @click="hideModal('challengeListModal')">Schließen</b-button>
        </b-modal>
      </div>
      <div id="landing" class="container container-onepage" ref="content">
        <div class="container">
          <h1>AmongUs Challenges</h1>
          <h2>Maps</h2>
          <center>
            <div>
              <a href="#" @click="showMapModal('TheSkeld')">
                <img rounded src="../assets/images/games/TheSkeldButton.png" />
              </a>
              <a href="#" @click="showMapModal('MiraHQ')">
                <img rounded src="../assets/images/games/MiraHQButton.png" />
              </a>
              <a href="#" @click="showMapModal('Polus')">
                <img rounded src="../assets/images/games/PolusButton.png" />
              </a>
            </div>
          </center>
          <h2
            @click="generateNewChallenges()"
            v-b-tooltip.hover
            title="Klicke hier um neue Challenges zu generieren."
          >
            Challenge Generator
          </h2>
          <br />
          <main>
            <b-card-group columns>
              <b-card class="text-center" bg-variant="dark" header-tag="header">
                <template #header>
                  General Challenge
                  <a
                    v-b-tooltip.hover
                    title="Generelle Challenges gelten unabhängig von der Rolle. Sollten sich zwei Challenges ausschließen, hat die generelle Challenge eine GERINGERE Priorität."
                    @click="showChallengeListModal('General')"
                    ><icon
                      name="question-circle"
                      scale="1"
                      class="symbol"
                    ></icon
                  ></a>
                </template>
                {{ generalChallenge }}
              </b-card>
              <b-card
                header="Crewmate Challenge"
                class="text-center"
                bg-variant="primary"
                header-tag="header"
              >
                <template #header>
                  Crewmate Challenge
                  <a
                    v-b-tooltip.hover
                    title="Crewmate Challenges gelten, wenn du Crewmate bist."
                    @click="showChallengeListModal('Crewmate')"
                    ><icon
                      name="question-circle"
                      scale="1"
                      class="symbol"
                    ></icon
                  ></a>
                </template>
                {{ crewmateChallenge }}
              </b-card>
              <b-card
                header="Impostor Challenge"
                class="text-center"
                bg-variant="danger"
                header-tag="header"
              >
                <template #header>
                  Impostor Challenge
                  <a
                    v-b-tooltip.hover
                    title="Impostor Challenges gelten, wenn du Impostor bist."
                    @click="showChallengeListModal('Impostor')"
                    ><icon
                      name="question-circle"
                      scale="1"
                      class="symbol"
                    ></icon
                  ></a>
                </template>
                {{ impostorChallenge }}
              </b-card>
            </b-card-group>
          </main>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { sendJsonRequest } from '../services/utility-functions/send-json-request.js'
const dataURL = 'https://raw.githubusercontent.com/TheFinalSkill/TheFinalSkill-Website/main/AmongUsChallenges.json'
export default {
  name: 'AmongUs',
  data () {
    return {
      generalChallenges: [],
      crewmateChallenges: [],
      impostorChallenges: [],
      gameChallenges: [],
      generalChallenge: '',
      crewmateChallenge: '',
      impostorChallenge: '',
      gameChallenge: '',
      mapModalImgSrc: '',
      challengeList: '',
      challengeListModalColor: 'dark'
    }
  },
  methods: {
    getData: async function () {
      const amongUsChallenges = await sendJsonRequest(dataURL)
      this.generalChallenges = amongUsChallenges.generalChallenges
      this.crewmateChallenges = amongUsChallenges.crewmateChallenges
      this.impostorChallenges = amongUsChallenges.impostorChallenges
      this.gameChallenges = amongUsChallenges.gameChallenges
      this.generateNewChallenges()
    },
    newTab: function (link) {
      window.open(link, '_blank')
    },
    scrollToTop () {
      window.scrollTo(0, 0)
    },
    generateNewChallenges () {
      this.generalChallenge = this.generalChallenges[Math.floor(Math.random() * this.generalChallenges.length)]
      this.crewmateChallenge = this.crewmateChallenges[Math.floor(Math.random() * this.crewmateChallenges.length)]
      this.impostorChallenge = this.impostorChallenges[Math.floor(Math.random() * this.impostorChallenges.length)]
      this.$bvToast.toast('Neue Challenges wurden generiert.', {
        title: 'Among Us Challenges',
        variant: 'success',
        autoHideDelay: 500,
        solid: true
      })
    },
    generateNewGameChallenge () {
      this.gameChallenge = this.gameChallenges[Math.floor(Math.random() * this.gameChallenges.length)]
    },
    hideModal (modalName) {
      this.$refs[modalName].hide()
    },
    showMapModal (mapname) {
      this.$refs['mapModal'].show()
      this.$refs['mapModal'].title = 'Map ' + mapname
      switch (mapname) {
        case 'TheSkeld':
          this.mapModalImgSrc =
            'https://prod.cdn.earlygame.com/uploads/images/_imageBlock/SKELD_MAP-1.jpg?mtime=20201022122655&focal=none&tmtime=20201022132145'
          break
        case 'Polus':
          this.mapModalImgSrc =
            'https://prod.cdn.earlygame.com/uploads/images/_imageBlock/3904024/Polus.jpg?mtime=20201022125504&focal=none&tmtime=20201022132152'
          break
        case 'MiraHQ':
          this.mapModalImgSrc =
            'https://prod.cdn.earlygame.com/uploads/images/_imageBlock/3900598/Mirahq.jpg?mtime=20201022124132&focal=none&tmtime=20201022132151'
          break
      }
    },
    showChallengeListModal (ChallengeType) {
      let modal = this.$refs['challengeListModal']
      switch (ChallengeType) {
        case 'General':
          modal.title = 'General Challenges (' + this.generalChallenges.length + ')'
          this.challengeListModalColor = 'dark'
          this.challengeList = this.generalChallenges
          break
        case 'Crewmate':
          modal.title = 'Crewmate Challenges (' + this.crewmateChallenges.length + ')'
          this.challengeListModalColor = 'primary'
          this.challengeList = this.crewmateChallenges
          break
        case 'Impostor':
          modal.title = 'Impostor Challenges (' + this.impostorChallenges.length + ')'
          this.challengeListModalColor = 'danger'
          this.challengeList = this.impostorChallenges
          break
        case 'Lobby':
          modal.title = 'Lobby Challenges (' + this.gameChallenges.length + ')'
          this.challengeListModalColor = 'dark'
          this.challengeList = this.gameChallenges
          break
      }
      modal.show()
    }
  },
  mounted: function () {
    this.getData()
  }
}
</script>

<style lang="scss" scoped>
@import "../style/bootstrap-component-include";
@import "../style/pageStyle.scss";

.container-onepage {
  height: calc(100vh);
  display: table;

  .row {
    display: table-row;

    .col {
      display: table-cell;
      vertical-align: middle;
      min-height: 0;
      .row-aligned-content {
        text-align: center;

        img {
          margin-bottom: 25px;
          max-width: 80%;
        }
      }
    }
  }
}

#landing {
  > div {
    background-color: rgba(0, 0, 0, 0.45);
  }

  color: #FFFFFF;
}

#landing-img {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  object-fit: cover;
}

.card-columns {
  column-count: 3;
}

.all {
  background-color: rgb(41, 49, 51);
  background: url("../assets/images/AmongUsBackground.jpg") no-repeat center
    fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

.noSelect {
    -ms-user-select: None;
    -moz-user-select: None;
    -webkit-user-select: None;
    user-select: None;
  }

#TheFinalSkill {
  position: fixed;
  right: 0px;
  bottom: 0px;
  width: 120px;
  color: rgba(255, 255, 255, 0.5);
}

#new-challenges {
  position: fixed;
  right: 20px;
  bottom: 20px;
  width: 200px;
}

#new-game-challenge {
  position: fixed;
  right: 20px;
  bottom: 70px;
  width: 200px;
}

#feedback-button {
  position: fixed;
  left: 20px;
  bottom: 20px;
  width: 200px;
}
</style>
